.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: relative;
  top: 0;
 
  background: white;
  padding-top: 16px;
  padding-bottom: 16px;
  
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

.article-body {
  margin-left: 300px;
  margin-right: 300px;
  text-align: left;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
  margin-left: 250px;
  margin-right: 250px;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

.comments p {
  text-decoration: none;
  color: black;
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

.upvoteButton, .commentButton {
  background-color: black;
  color: white;
  border: 2px solid #555555;
  border-radius: 8px;
  padding : 10px;
  margin: 8px 8px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.upvoteButton:hover,.commentButton:hover {
  background-color: #555555;
  color: white;
}

.comment-form{
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  display: block;
}
.comment-form label input[type=text],textarea {
  display:block;
  margin : 10px;
  border-radius : 5px;
}

